import { Provider } from "react-redux";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import store from "./store";

import Notfound from './pages/Notfound';

import Default from './pages/Default';
import Defaultcoursetwo from './pages/Defaultcoursetwo';
import Defaultcourseone from './pages/Defaultcourseone';
// import Defaultcategory from './pages/Defaultcategory';
// import Defaultfollower from './pages/Defaultfollower';
import Defaultsettings from './pages/Defaultsettings';
import Defaultuserprofile from './pages/Defaultuserprofile';
import Accountinfo from './pages/Accountinfo';

import Password from './pages/Password';
// import Defaultsearch from './pages/Defaultsearch';
// import Defaultchannel from './pages/Defaultchannel';
// import Defaultlive from './pages/Defaultlive';
// import Defaultauthorprofile from './pages/Defaultauthorprofile';
// import Defaultanalytics from './pages/Defaultanalytics';




function App() {
    const path = process.env.PUBLIC_URL;
    return (
        <Provider store={store}>
            <Router>
                <Switch>
                    {/* Curso */}
                    <Route exact path={`${path}/`}  component={Default} /> 
                    <Route exact path={`${path}/home`} component={Default} />
                    <Route exact path={`${path}/course-details`} component={Defaultcoursetwo} />
                    <Route exact path={`${path}/view-course`} component={Defaultcourseone} />

                    {/* AJustes */}
                    <Route exact path={`${path}/settings`} component={Defaultsettings} />
                    <Route exact path={`${path}/default-user-profile`} component={Defaultuserprofile} />
                    <Route exact path={`${path}/account-information`} component={Accountinfo} />

                    <Route exact path={`${path}/password`} component={Password} />





                    <Route exact path={`${path}/*`} component={Notfound} />
                </Switch>
            </Router>
        </Provider>
    );
}

export default App;